<template>
  <view class="main">
    <view class="select-modal" :class="'show'" @tap="hideModal">
      <view class="select-dialog" @tap.stop="" :style="{ backgroundColor: bgColor }">
        <view class="title-main">
          <text class="title-detail">{{ title }}</text>
        </view>

        <view class="search-box" v-if="showSearch">
          <input class="search-input" confirm-type="search" v-model="searchInput" placeholder="输入内容进行模糊查询"
            placeholder-style="color:rgba(102, 102, 102, 0.25);" />
          <text v-if="showSearchBtn" class="search-text" @click="handleSearch" style="color: #fff;">
            搜索
          </text>
        </view>
        <view class="recommandPoint">
          <text style="left: 10px; color: #666; font-size: 15px; font-weight: bold;">猜你想去</text>
          <view class="tag_container">
            <zyTag v-for="(item,index) in recommendations" :text="item.attractionName" size="medium" circle="true"
              v-bind:theme="item.checked===true?'dark':'plain'" @click="multiHandle(item,index)" />
          </view>
        </view>

        <view class="select-content">
          <view class="select-item" v-for="(item, index) in list" :key="index" :style="
              valueIndexOf(item)
                ? 'color:' +
                  selectColor +
                  ';background-color:' +
                  selectBgColor +
                  ';'
                : 'color:' + color + ';'
            " @click="select(item)">
            <view class="title">{{ getLabelKeyValue(item) }}</view>
            <text class="selectIcon icongou" v-if="valueIndexOf(item)"></text>
          </view>
        </view>
        <view class="select-bar bg-white">
          <button plain="true" class="mini-btn action" type="default" size="default" @tap="empty">
            {{ emptyText }}
          </button>
          <button class="mini-btn action" style="background-color: #47a6ff;" type="primary" size="default"
            @tap="confirmClick">
            {{ confirmText }}
          </button>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
  import zyTag from '@/uni_modules/zy-tag/components/zy-tag/zy-tag.vue'
  import userApi from '@/api/userApi.js'
  import {
    mapGetters,
    mapState,
    mapMutations
  } from 'vuex'

  export default {
    name: 'zqsSelect',
    components: {
      zyTag
    },
    computed: {
      ...mapState('m_roadMsg', ['allPointList']),
    },
    data() {
      return {
        type: "default",
        searchInput: '',
        options: [],
        recommendations: [],
      }
    },
    created() {
      // 在父组件的 created 生命周期钩子函数中等待子组件的创建完成
      this.$nextTick(() => {
        // 在回调函数中初始化数据
        userApi.getRecommendation().then(res => {
          this.recommendations = res
          this.recommendations.forEach((item, index) => {
            this.$set(this.recommendations[index], 'checked', false)
          })
        })
      })
    },
    watch: {
      searchInput(val) {
        if (!this.$props.showSearchBtn) this.$emit('search', val)
      },
      recommendations: {
        immediate: true, // 立即执行
        deep: true, // 深度侦听复杂类型内变化
        handler(newVal, oldVal) {
          this.recommendations = newVal
        }
      }
    },
    props: {
      showSearch: {
        // 是否显示搜索框
        type: Boolean,
        default: true,
      },
      value: {
        type: [Number, String, Array, Object],
        default: null,
      },
      placeholder: {
        // 占位符
        default: '',
        type: String,
      },
      multiple: {
        // 是否多选
        default: true,
        type: Boolean,
      },
      list: {
        default: () => [],
        type: Array,
      },
      valueKey: {
        // 指定List中valueKey的值作为下拉框绑定内容
        default: 'value',
        type: String,
      },
      labelKey: {
        // 指定List中labelKey的值作为下拉框显示内容
        default: 'label',
        type: String,
      },
      disabled: {
        default: false,
        type: Boolean,
      },
      clearable: {
        default: false,
        type: Boolean,
      },
      emptyText: {
        default: '重置',
        type: String,
      },
      title: {
        default: '请选择你想去的景点',
        type: String,
      },
      confirmText: {
        default: '确定',
        type: String,
      },
      color: {
        default: '#000000',
        type: String,
      },
      selectColor: {
        default: '#0081ff',
        type: String,
      },
      bgColor: {
        default: '#ffffff',
        type: String,
      },
      selectBgColor: {
        default: '#FFFFFF',
        type: String,
      },
      valueType: {
        default: 'single',
        type: String, // single || all
      },
      showSearchBtn: {
        default: true,
        type: Boolean, // single || all
      },
      showArrow: {
        type: Boolean,
        default: true,
      },
    },
    emits: ['openDeepScroll', 'closeDeepScroll'],
    computed: {
      ...mapState('m_settings', ['isShowModal']),

      _value: {
        get() {
          return this.get_value(this.value)
        },
        set(val) {
          this.$emit('input', val)
        },
      },
    },
    methods: {
      ...mapMutations('m_settings', ['updateIsShowModal']),

      multiHandle(item, index) {
        this.recommendations[index].checked = !this.recommendations[index].checked
      },
      handleSearch() {
        this.$emit('search', this.searchInput)
      },
      get_value(val) {
        // 将数组值转换为以,隔开的字符串
        if (val || val === 0) {
          if (Array.isArray(val)) {
            let chooseAttr = []
            val.forEach((item) => {
              let choose = this.list.find((temp) => {
                let val_val = this.getValueKeyValue(temp)
                return item === val_val
              })
              // 判断是否存在
              if (choose) {
                chooseAttr.push(choose)
              }
            })
            let values = ''
            if (chooseAttr.length > 0) {
              values = chooseAttr
                .map((temp) => this.getLabelKeyValue(temp))
                .join(',')
            }

            return values
          } else {
            let choose = this.list.find((temp) => {
              let val_val = this.getValueKeyValue(temp)
              return val === val_val
            })
            if (choose) {
              return this.getLabelKeyValue(choose)
            } else {
              return val
            }
          }
        } else {
          return ''
        }
      },
      select(item) {
        // 点击选项
        let val = this.getValueKeyValue(item)
        if (this.multiple) {
          let _value = this.value
          let index = _value ? _value.indexOf(val) : -1
          if (index != -1) {
            _value.splice(index, 1)
            this.options.splice(index, 1)
            this.$emit('input', _value)
          } else {
            _value.push(val)
            this.options.push(item)
            this.$emit('input', _value)
          }
          this.$emit('change', item)
        } else {
          let label = this.getLabelKeyValue(item)
          if (this._value) {
            if (label.indexOf(this._value) !== -1) {
              this.$emit('input', '')
            } else {
              this.$emit('input', val)
            }
          } else {
            this.$emit('input', val)
          }

          // 单选选中的当前项所有
          this.$emit('change', item)
          this.hideModal()
        }
      },
      valueIndexOf(item) {
        let val = this.getValueKeyValue(item)
        if (Array.isArray(this.value)) {
          return this.value.indexOf(val) != -1
        } else {
          return this.value === val
        }
      },
      getLabelKeyValue(item) {
        // 获取label
        return item[this.labelKey]
      },
      getValueKeyValue(item) {
        // 获取value
        return item[this.valueKey]
      },
      empty() {
        this.recommendations.forEach(item => {
          item.checked = false
        })
        // 清空
        if (this.multiple) {
          this.$emit('change', [])
          this.$emit('input', [])
        } else {
          this.$emit('change', '')
          this.$emit('input', '')
        }
      },
      // cancelClick() {
      //   // 点击取消
      //   this.$emit('cancel', this._value)
      //   this.hideModal()
      // },

      confirmClick() {
        this.recommendations.forEach(item => {
          if (item.checked) {
            const recom = this.options.find(res => res.id === item.attractionId)
            if (recom === undefined) {
              item.id = item.attractionId
              this.options.push(item)
            }
          }
        })
        const ids = this.options.map(item => item.id)
        const uniqueIds = ids.filter((id, index, self) => self.indexOf(id) === index) // 对新数组进行去重操作
        // 点击确定
        if (this.valueType !== 'all') {
          this.$emit('confirm', uniqueIds)
        } else {
          console.log(this._value);
          this.$emit('confirm', this._value)
        }
        this.hideModal()
      },
      showModal() {
        // 显示model
        if (!this.disabled) {
          this.updateIsShowModal('true')
          // 打开禁止穿透滚动
          this.$emit('openDeepScroll')
        }
      },
      hideModal() {
        // 隐藏model
        this.updateIsShowModal('false')
        // 关闭禁止穿透滚动
        this.$emit('closeDeepScroll')
      },
    },
  }
</script>
<style>
  @font-face {
    font-family: 'selectIcon';
    src: url('//at.alicdn.com/t/font_1833441_ycfzdhg2u3.eot?t=1590375117208');
    /* IE9 */
    src: url('//at.alicdn.com/t/font_1833441_ycfzdhg2u3.eot?t=1590375117208#iefix') format('embedded-opentype'),
      /* IE6-IE8 */
      url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2'),
      url('//at.alicdn.com/t/font_1833441_ycfzdhg2u3.woff?t=1590375117208') format('woff'),
      url('//at.alicdn.com/t/font_1833441_ycfzdhg2u3.ttf?t=1590375117208') format('truetype'),
      /* chrome, firefox, opera, Safari, Android, iOS 4.2+ */
      url('//at.alicdn.com/t/font_1833441_ycfzdhg2u3.svg?t=1590375117208#selectIcon') format('svg');
    /* iOS 4.1- */
  }

  .tag_container {
    flex-direction: row;
    flex-wrap: wrap;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .recommandPoint {
    border-bottom: 1px solid #f5f2f2;
    padding-bottom: 5px;
  }

  .title-main {
    display: flex;
    justify-content: center;
    width: 100%;
  }

  .title-detail {
    display: flex;
    width: 88%;
    justify-content: center;
    padding: 30rpx 0;
    font-size: 20px;
    font-weight: 600;
    color: #666;
    /*  border-bottom: 1rpx solid #e6e1e1; */
  }

  .selectIcon {
    font-family: 'selectIcon' !important;
    font-size: 16px;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  .icongou:before {
    content: '\e61c';
  }

  .iconcross:before {
    content: '\e61a';
  }
</style>
<style lang="scss" scoped>
  .main {
    font-size: 28rpx;
  }

  .bg-white {
    background-color: #ffffff;
  }

  .input {
    display: flex;
    align-items: center;
    // font-size: 28rpx;
    // height: 60rpx;
    // padding: 10rpx 20rpx;
    // border-radius: 10rpx;
    // border-style: solid;
    // border-width: 1rpx;
    // border-color: rgba(0, 0, 0, 0.1);

    input {
      flex: 1;
      text-align: right;
      color: #333333;
    }

    .selector-icon {
      width: 32rpx;
      height: 36rpx;
      vertical-align: middle;
    }
  }

  .select-modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 9999;
    opacity: 0;
    outline: 0;
    text-align: center;
    -ms-transform: scale(1.185);
    transform: scale(1.185);
    backface-visibility: hidden;
    perspective: 2000rpx;
    background: rgba(0, 0, 0, 0.6);
    transition: all 0.3s ease-in-out 0s;
    pointer-events: none;
    margin-bottom: -1000rpx;

    &::before {
      content: '\200B';
      display: inline-block;
      height: 100%;
      vertical-align: bottom;
    }

    .select-dialog {
      position: absolute;
      left: 0;
      bottom: 0;
      display: inline-block;
      margin-left: auto;
      margin-right: auto;
      background-color: #f8f8f8;
      overflow: hidden;
      width: 100%;
      border-radius: 0;

      .select-content {
        // background-color: #F1F1F1;
        height: 60vh;
        overflow: auto;

        .select-item {
          text-align: left;
          padding: 20rpx 80rpx;
          display: flex;

          ::after {
            content: '';
            width: 100%;
            height: 1px;
            display: block;
            margin: 0 auto;
            border-bottom: 2px solid #f5f2f2;
            padding: 1px;
          }

          .title {
            flex: 1;
          }
        }
      }
    }
  }

  .select-modal.show {
    opacity: 1;
    transition-duration: 0.3s;
    -ms-transform: scale(1);
    transform: scale(1);
    overflow-x: hidden;
    overflow-y: auto;
    pointer-events: auto;
    margin-bottom: 0;
  }

  .select-bar {
    padding: 0 80rpx;
    display: flex;
    position: relative;
    align-items: center;
    min-height: 80rpx;
    justify-content: space-between;
    margin-bottom: 50rpx;

    .action {
      display: flex;
      align-items: center;
      height: 78rpx;
      justify-content: center;
      max-width: 100%;
      padding: 0 100rpx;
    }
  }

  .search-box {
    display: flex;
    margin: 10rpx 0;
    align-items: center;
    padding: 10rpx 40rpx;
  }

  .search-input {
    display: flex;
    flex: 1;
    // width: 560rpx;
    height: 67rpx;
    line-height: 67rpx;
    border-radius: 40rpx;
    background: #f5f2f2;
  }

  .search-text {
    color: #ffffff;
    padding-left: 30rpx;
  }
</style>